import React, { useState, useRef } from 'react';
import { connect } from 'react-redux';
import { Form, Switch, Modal, Button, Row, Col, Input, Typography } from 'antd';
import BankDocs from './BankDocs';
import SuperFetch from '../../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import jwtDecode from 'jwt-decode';
import BankInfoStyleWrapper from './bankInfo.style';
import Editor from '@isomorphic/shared/UIView/Editor/Editor';

function Assessment(props) {
  // Declare a new state variable, which we'll call "count"
  const [disabled] = useState(true);

  const [bankDetailsVisible, setBankDetailsVisible] = useState(false);
  const [decisionStatus, setdecisionStatus] = useState(null);
  const [emailVisible, setEmailVisible] = useState(false);
  const [decisionMsg, setdecisionMsg] = useState(null);
  const inputEl = useRef(null);
  const [accessment, setAssessment] = useState({});
  const { getFieldDecorator } = props.form;
  const onChange = (e, a) => {
    let newAccessment = { ...accessment, [a.currentTarget.id]: e };
    setAssessment(newAccessment);
  };
  const disagree = (e, a) => {
    props.form.validateFields((err, values) => {
      if (!err) {
        setdecisionStatus('disagree');
        setdecisionMsg(values);
        setEmailVisible(true);
      }
    });
  };

  const approve = (e, a) => {
    props.form.validateFields((err, values) => {
      if (!err) {
        setdecisionStatus('approved');
        setdecisionMsg(values);
        setEmailVisible(true);
      }
    });
  };
  const handleSubmite = value => {
    let decision = {};
    decision.claimInfoId = props.claim.id;
    decision.createdBy = jwtDecode(props.idToken).id;
    decision.group = 'decision';
    decision.message = decisionMsg.decision;
    decision.email = inputEl.current.state.value;
    decision.contactEmail = props.claim.contactEmail;
    console.log(props.claim);
    let status = { decisionStatus };
    if (decisionStatus === 'disagree') {
      status.status = 're-pd';
    } else if (decisionStatus === 'approved') {
      switch (props.claim.status) {
        case 'pd-de':
          status.status = 'cd';
          break;
        case 'pd-co':
          status.status = 'cc';
          break;
        case 'pd-pa':
          status.status = 'cp';
          break;
        case 'pd-ap':
          status.status = 'ca';
          break;
        default:
          notification('error', 'Something wrong in status');
          return false;
      }
    } else {
      return;
    }
    console.log(decision);
    SuperFetch.post('evaluate/decision', { decision, status })
      .then(response => {
        if (response.status === 200) {
          console.log(response);
          notification('success', 'Email has been sent out');
          setEmailVisible(false);
          props.history.goBack();
        } else {
          notification('error', 'Something wrong in retrieving data');
        }
      })
      .catch(e => console.log(e));
  };
  return (
    <Form layout="inline" colon={false}>
      <Col xs={24} sm={20} md={20} lg={20} xl={20} xxl={8}>
        <Row>
          <Typography.Title level={4}>This Policy:</Typography.Title>
        </Row>
        <Col span={10}>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span>Is this claim for a Pre-Existing condition?</span>
            </Col>
            <Col span={8}>
              <Switch
                id={'forPreExisting'}
                disabled={disabled}
                onChange={onChange}
                checked={
                  props.claim && props.claim.Acessment
                    ? props.claim.Acessment.forPreExisting
                    : false
                }
              />
            </Col>
          </Row>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span>Does it realte to a Pre-Exsting condition?</span>
            </Col>
            <Col span={8}>
              <Switch
                id={'relatePreExisting'}
                disabled={disabled}
                onChange={onChange}
                checked={
                  props.claim && props.claim.Acessment
                    ? props.claim.Acessment.relatePreExisting
                    : false
                }
              />
            </Col>
          </Row>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span>Do any other Policy Exclusions apply?</span>
            </Col>
            <Col span={8}>
              <Switch
                id={'otherExclusion'}
                disabled={disabled}
                onChange={onChange}
                checked={
                  props.claim && props.claim.Acessment
                    ? props.claim.Acessment.otherExclusion
                    : false
                }
              />
            </Col>
          </Row>
        </Col>
        <Col span={12} offset={2}>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span style={{ display: 'block' }}>
                Are all the charges reasonable and customary?
              </span>
            </Col>
            <Col span={8}>
              <Switch
                id={'reasonable'}
                disabled={disabled}
                onChange={onChange}
                checked={
                  props.claim && props.claim.Acessment
                    ? props.claim.Acessment.reasonable
                    : false
                }
              />
            </Col>
          </Row>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span style={{ display: 'block' }}>
                Does this claim adhere to the Policy Workding?
              </span>
            </Col>
            <Col span={8}>
              <Switch
                id={'adhere'}
                disabled={disabled}
                onChange={onChange}
                checked={
                  props.claim && props.claim.Acessment
                    ? props.claim.Acessment.adhere
                    : false
                }
              />
            </Col>
          </Row>
        </Col>
      </Col>
      <Col xs={24} sm={4} md={4} lg={4} xl={4} xxl={4}>
        <Row>
          <Typography.Title level={4}>Documents:</Typography.Title>
        </Row>
        <Button type="primary" onClick={() => setBankDetailsVisible(true)}>
          FILE
        </Button>
        <BankInfoStyleWrapper>
          <Modal
            width={'70%'}
            visible={bankDetailsVisible}
            onOk={() => setBankDetailsVisible(false)}
            onCancel={() => setBankDetailsVisible(false)}
            footer={[
              <Button
                key={1}
                type="primary"
                onClick={() => setBankDetailsVisible(false)}
              >
                OK
              </Button>,
            ]}
          >
            <BankDocs key={1} claim={props.claim} />
          </Modal>
        </BankInfoStyleWrapper>
      </Col>
      <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={12}>
        <Col span={8}>
          <Row>
            <Typography.Title level={4}>Rationale:</Typography.Title>
          </Row>
          <span>
            {props.claim && props.claim.Comments
              ? props.claim.Comments.sort((a, b) => {
                  if (a.group === 'evaluate') return -1;
                  return new Date(a.updatedAt) - new Date(b.updatedAt);
                })[0].message
              : null}
          </span>
        </Col>
        <Col span={8}>
          <Row>
            <Typography.Title level={4}>Decision:</Typography.Title>
          </Row>
          <Form.Item style={{ width: '100%' }}>
            {getFieldDecorator('decision', {
              rules: [
                {
                  required: true,
                  message: 'Please input your decision',
                },
              ],
            })(
              <Input.TextArea
                placeholder="Reason for decision..."
                cols={100}
                autoSize={{ minRows: 4 }}
              />
            )}
          </Form.Item>
        </Col>
        <Col span={6} offset={2}>
          <Form.Item>
            <Button style={{ background: '#DB2828' }} onClick={disagree}>
              DISAGREE
            </Button>
            <Button style={{ background: '#1BA12C' }} onClick={approve}>
              APPROVE - {props.claim ? props.claim.status : null}
            </Button>
          </Form.Item>
        </Col>
      </Col>
      <Modal
        title="Decision email sent to the client"
        visible={emailVisible}
        onOk={handleSubmite}
        onCancel={() => setEmailVisible(false)}
        footer={[
          <Button key="submit" type="primary" onClick={handleSubmite}>
            Submit
          </Button>,
        ]}
      >
        <Editor
          ref={inputEl}
          defaultPlaceHolder={decisionMsg ? decisionMsg.decision : null}
        />
      </Modal>
    </Form>
  );
}

export default connect(
  state => ({ idToken: state.Auth.idToken }),
  {}
)(Form.create()(Assessment));
